import { createDbWorker } from "sql.js-httpvfs";
import HeadSearch from '../components/HeadSearch.vue';

const publicPath =
  process.env.NODE_ENV === "production" ? "/" : "/";

const workerUrl = new URL(
  `${publicPath}sql.js-httpvfs/sqlite.worker.js`,
  ({ url: getAbsoluteUrl('src/views/Organisme.vue?vue&type=script&lang=js&') }).url
);
const wasmUrl = new URL(
  `${publicPath}sql.js-httpvfs/sql-wasm.wasm`,
  ({ url: getAbsoluteUrl('src/views/Organisme.vue?vue&type=script&lang=js&') }).url
);

export default {
  name: 'Organisme',
  mixins: [],
  components:{
    HeadSearch
  },
  data(){
    return {
      result: undefined,
      columns: ['denomination','siren',''],
      id: ''
    }
  },
  props: {
  },
  computed: {
  },
  async mounted() {
    this.worker = await createDbWorker(
      [
        {
          from: "inline",
          config: {
            serverMode: "full",
            url: `${publicPath}db/of.sqlite`,
            requestChunkSize: 4096,
          },
        },
      ],
      workerUrl.toString(),
      wasmUrl.toString()
    );
    this.id = this.$route.params.id
    this.searchText();
  },
  methods: {
    gotoae(siren){
      window.location.href = 'https://annuaire-entreprises.data.gouv.fr/entreprise/'+siren
    },
    searchText(){
      this.runSql("SELECT * FROM complete WHERE numeroDeclarationActivite = '" + this.id + "' LIMIT 1")
    },
    async runSql(sql) {
      this.result = await this.worker.db.query(sql);
    },
  },
  watch:{
  },
  created(){
  },


}
